.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-transform: capitalize;
}

.components {
    border-bottom: 2px solid rgba(59, 59, 59, 1);
    padding: 20px 0;
    width: var(--width-header);
    display: flex;
    align-items: center;
    justify-content: center;

    &:last-child {
        border-bottom: none;
        padding-bottom: 0;
    }
}

.text {
    font-size: 5rem;
    line-height: 5rem;
    padding: 20px 0;
}

.icon {
    width: 350px;
    height: 320px;
    padding: 20px 0;
}
